// 
import store from "@/store"

export default {
  //
  success(data) {
    store.commit("message", {
      type: "success", //message
      message: data.message ? data.message : data, //message
      time: data.time ? data.time : 3000, //message3s
      light: data.light ? data.light : false //messagedark
    })
  },

  //
  error(data) {
    store.commit("message", {
      type: "error",
      message: data.message ? data.message : data,
      time: data.time ? data.time : 3000,
      light: data.light ? data.light : false
    })
  },

  //
  info(data) {
    store.commit("message", {
      type: "info",
      message: data.message ? data.message : data,
      time: data.time ? data.time : 3000,
      light: data.light ? data.light : false
    })
  },

  //
  warning(data) {
    store.commit("message", {
      type: "warning",
      message: data.message ? data.message : data,
      time: data.time ? data.time : 3000,
      light: data.light ? data.light : false
    })
  },
}