<template>
  <div>
    <v-container fluid>
      <v-card elevation="5">
        <v-img src="@/assets/h5-partner-banner.png">
          <v-card-subtitle class="d-flex justify-center grey--text text--lighten-2 text-subtitle-1 mt-10">
            
          </v-card-subtitle>
          <v-card-title class="d-flex justify-center white--text pt-0">
             
          </v-card-title>
        </v-img>

        <v-card-title></v-card-title>

        <v-form
          ref="form"
          v-model="valid"
          lazy-validation
          class="mx-4 pb-4"
        >
          <v-text-field
            v-model="companyName"
            :rules="[v => !!v || '']"
            label=""
            clearable
            required
          />
          <v-text-field
            v-model="companyAddress"
            :rules="[v => !!v || '']"
            label=""
            clearable
            required
          />
          <v-text-field
            v-model="name"
            :rules="[v => !!v || '']"
            label=""
            clearable
            required
          />
          <v-text-field
            v-model="mobile"
            :rules="mobileRules"
            :counter="11"
            label=""
            clearable
            required
          />
          <v-row>
            <v-col cols="7">
              <v-text-field
                v-model="mobileCode"
                :rules="[v => !!v || '']"
                label=""
                clearable
                required
              />
            </v-col>
            <v-col
              cols="5"
              class="d-flex align-center"
            >
              <v-btn
                :disabled="smsDisabled"
                color="success"
                elevation="2"
                small
                width="120"
                @click="sms"
              >
                {{ smsBtn }}
              </v-btn>
            </v-col>
          </v-row>
          <v-text-field
            v-model="email"
            :rules="emailRules"
            label=""
            clearable
            required
          />
          <v-select
            v-model="industry"
            :items="industrys"
            :menu-props="{ top: true, offsetY: true }"
            :rules="[v => !!v || '']"
            label=""
            required
          />
          <v-text-field
            v-model="sales_num"
            :rules="[v => !!v || '']"
            label=""
            clearable
            required
          />
          <v-text-field
            v-model="product"
            :rules="[v => !!v || '']"
            label=""
            clearable
            required
          />

          <v-card-subtitle
            class="pl-0 pb-0 text-subtitle-1"
          >
            
          </v-card-subtitle>
          <v-row class="pt-0">
            <v-col cols="4">
              <v-select
                v-model="province"
                :items="provinces"
                item-text="name"
                item-value="id"
                :menu-props="{ top: true, offsetY: true }"
                :rules="[v => !!v || '']"
                label=""
                required
                @change="getCitys"
              />
            </v-col>
            <v-col cols="4">
              <v-select
                v-model="city"
                :items="citys"
                item-text="name"
                item-value="id"
                :menu-props="{ top: true, offsetY: true }"
                :rules="[v => !!v || '']"
                label=""
                required
                @change="getDistricts"
              />
            </v-col>
            <v-col cols="4">
              <v-select
                v-model="district"
                :items="districts"
                item-text="name"
                item-value="id"
                :menu-props="{ top: true, offsetY: true }"
                :rules="[v => !!v || '']"
                label=""
                required
              />
            </v-col>
          </v-row>
          
          <div class="d-flex justify-space-around">
            <v-btn
              :disabled="!valid"
              color="primary"
              elevation="2"
              @click="submit"
            >
              
            </v-btn>
          </div>
        </v-form>
      </v-card>

      <v-card elevation="5" class="mt-4">
        <v-card-title
          class="d-flex justify-space-around"
        >
          VIP: 15257569960
        </v-card-title>
        <v-img src="@/assets/h5-partner-bottom.png" />
      </v-card>
    </v-container>
  </div>
</template>

<script>
import {
  getArea,
  sendSms,
  applyAgent
} from '@/utils/api'

export default {
  data: () => ({
    valid: true,
    companyName: '',
    companyAddress: '',
    name: '',
    mobile: '',
    mobileRules: [
      v => !!v || '',
      v => /^1[3|4|5|7|8][0-9]\d{8}$/.test(v) || ''
    ],
    mobileCode: '',
    smsBtn: '',
    smsDisabled: false,
    email: '',
    emailRules: [
      v => !!v || '',
      v => /.+@.+\..+/.test(v) || ''
    ],
    industry: '',
    industrys: [
      '',
      '',
      'k12',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
    ],
    sales_num: '',
    product: '',
    province: null,
    provinceName: '',
    provinces: [],
    city: null,
    cityName: '',
    citys: [],
    district: null,
    districtName: '',
    districts: []
  }),

  watch: {
    province(selected, current) {
      this.provinces.map(val => {
        if (val.id === selected) {
          this.provinceName = val.name
        }
      })
      if (selected !== current) {
        this.city = ''
        this.district = ''
      }
    },
    city(selected, current) {
      this.citys.map(val => {
        if (val.id === selected) {
          this.cityName = val.name
        }
      })
      if (selected !== current) {
        this.district = ''
      }
    },
    district(selected) {
      this.districts.map(val => {
        if (val.id === selected) {
          this.districtName = val.name
        }
      })
    }
  },

  mounted() {
    this.getProvinces()
  },

  methods: {
    submit () {
      return new Promise(() => {
        applyAgent({
          company_name: this.companyName,
          company_address: this.companyAddress,
          name: this.name,
          mobile: this.mobile,
          mobile_code: this.mobileCode,
          email: this.email,
          industry: this.industry,
          sales_num: this.salesNum,
          product: this.product,
          province_name: this.provinceName,
          city_name: this.cityName,
          district_name: this.districtName
        }).then(res => {
          if (res.code === 0) {
            this.$Message.success({
              message: "!",
              time: 3000, // 
              light: false, // true
            })
            this.$refs.form.reset() // 
          }
        })
      })
      // this.$refs.form.validate() // 
      // this.$refs.form.reset() // 
      // this.$refs.form.resetValidation() // 
      // console.log(this.provinceName, this.cityName, this.districtName)
    },
    countingDown() {
      let count = 60
      let codeTimeObj = null
      codeTimeObj = setInterval(() => {
        this.smsDisabled = true
        this.smsBtn = count + ''
        count--
        if (count < 1) {
          clearInterval(codeTimeObj)
          this.smsDisabled = false
          this.smsBtn = ''
        }
      }, 1000)
    },
    sms() {
      if (this.mobile) {
        return new Promise(() => {
          sendSms({
            mobile: this.mobile
          }).then(res => {
            if (res.code === 0) {
              this.countingDown()
              this.$Message.success({
                message: "",
                time: 3000,
                light: false,
              })
            }
          })
        })
      } else {
        this.$Message.warning({
          message: ",",
          time: 3000,
          light: false,
        })
      }
    },
    getProvinces() {
      return new Promise(() => {
        getArea({
          pid: 0
        }).then(res => {
          if (res.code === 0) {
            this.provinces = res.data
          }
        })
      })
    },
    getCitys(id) {
      return new Promise(() => {
        getArea({
          pid: id
        }).then(res => {
          if (res.code === 0) {
            this.citys = res.data
            this.searchExactDetialShow = false
          }
        })
      })
    },
    getDistricts(id) {
      return new Promise(() => {
        getArea({
          pid: id
        }).then(res => {
          if (res.code === 0) {
            this.districts = res.data
          }
        })
      })
    },
  },
}
</script>
